export default {
	
	data() {
		return {
			currentNumber: 0, // 用来判断active样式类是否显示
			currentNumberBtn: 0,
			statusChange: '',
			curPage: 1,
			status: 0,
			nameList: [],
			id: '',
			acid: '',
			logo:'',
			list: [
				// {
				// 	title: "币种详情"
				// },
				{
					title: "充提记录"
				}
			],
			twoBtn: [{
				title: '转出'
			}, {
				title: '转入'
			}],
			nameList2: [{
					leftText: '发行时间:',
					rightText: '2008-11-10'
				},
				{
					leftText: '发行总量:',
					rightText: '21,000,000'
				},
				{
					leftText: '流通总量：',
					rightText: '17,816,737'
				},
				{
					leftText: '众筹价格：',
					rightText: '17,816,737'
				},
				{
					leftText: '白皮书：',
					rightText: '17,816,737'
				},
				{
					leftText: '官网：',
					rightText: '17,816,737'
				},
				{
					leftText: '区块链查询：',
					rightText: '17,816,737'
				},
				{
					leftText: '手机钱包下载地址：',
					rightText: '17,816,737'
				},
				{
					leftText: 'PC钱包下载地址：',
					rightText: '17,816,737'
				}

			],
			money: '',
			forzen: '',
			price: '',
			coinId: '',
			showDetail: false,
			name:''
		};
	},
	onLoad(options) {			
		this.coinId = options.coinId;
		this.money = options.money;
		this.forzen = options.forzen;
		this.price = options.price;
		this.logo = options.logo;
		this.name = options.Name;
		//this.name = uni.getStorageSync('currencyName');
		this.getCoreDetail()
	},
	onPullDownRefresh() {
		this.getCoreDetail()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		currentInfo(index) {
			this.currentNumber = index;
			if(this.currentNumber == 1){
				this.showDetail=false
			}else{
				this.showDetail=true
			}
			
			//单个币种的充提记录
			uni.request({
				url: this.baseUrl + "/recharge-withdraw",
				data:{
					page:1,
					count:100000,
					Id:this.coinId
				},
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					 if (res.data.status == 1) {
						this.nameList = res.data.data.list
						console.log(JSON.stringify(this.nameList))

					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		//处理状态返回的显示值
		showStatus(status){
			if(status){
				if(status=="-1"){
					return "驳回" 
				}else if(status=="0"){
					
					return "待处理"
				}else if(status=="1"){
					
					return "处理中"
				}else if(status=="2"){
					return "已处理"
				}else if(status=="3"){
					
					return "失败"
				}else if(status=="4"){
					
					return "处理成功"
				}
			}
		},
		//状态不同显示不同的颜色
		showColor(status){
			if(status){
				if(status=="-1"){
					return "red" 
				}else if(status=="0"){
					
					return "green"
				}else if(status=="1"){
					
					return "green"
				}else if(status=="2"){
					return "blue"
				}else if(status=="3"){
					
					return "red"
				}else if(status=="4"){
					
					return "blue"
				}
			}
		},
		currentBtnIndex(index) {
			this.currentNumberBtn = index
		},
		//根据币种Id获取币种
		getCoreDetail() {
			uni.request({
				url: this.baseUrl + "/single-coin",
				data: {
					Id: this.coinId
				},
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else if (res.data.status == 1) {


					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
			//单个币种的充提记录
			uni.request({
				url: this.baseUrl + "/recharge-withdraw",
				data:{
					page:1,
					count:100000,
					Id:this.coinId
				},
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					 if (res.data.status == 1) {
						this.nameList = res.data.data.list
						console.log(JSON.stringify(this.nameList))
				
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		showState(state) {
			if (state == 1) {
				return "待理财";
			} else if (state == 2) {
				return "理财中"
			} else if (state == 3) {
				return "已撤资"
			} else if (state == 4) {
				return "已完结"
			}
		},
		jumpToManage(index) {
			this.id = this.nameList[index].Id
			this.acid = this.nameList[index].ActivityId
			console.log(this.id) //我的理财id
			console.log(this.acid) //理财活动id
			uni.navigateTo({
				url: "./manage-in?id=" + this.id + "&acid=" + this.acid //需要传一个investmentId过去,还需传一个acid过去获取天数
			})
		}
	}
}
